<template>
  <nav>
    <div class="static-header">
      <div class="main-logo">
        <img :src="require('../../assets/logo.jpg')" />
      </div>
      <div class="menu-icon">
        <i class="ion-ios-menu" @click="toggleNav"></i>
      </div>
    </div>
    <ul class="nav-items" ref="nav">
      <li v-for="(link, index) in navLinks" class="nav-item" :key="index">
        <div class="nav-item-link" @click="redirect(link.path)">
          {{ link.text }}
          <i :class="link.icon" icon />
        </div>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ["navLinks"],
  methods: {
    toggleNav() {
      const nav = this.$refs.nav.classList;
      nav.contains("active") ? nav.remove("active") : nav.add("active");
    },
    redirect(path) {
      this.toggleNav();
      this.$router.push(path);
    }
  }
};
</script>

